
body {
    background-image: url("https://zerogeorge.com/wp-content/uploads/2017/08/ZeroGeorge_Cooking-School_EricKelley-1568x824-c-default.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

a {
    color: #2c3b4b
}

h3 {
    font-family: 'Teko', sans-serif;
    font-size: 1.7em;
}

