#myInput {
    display: block;
    margin-top: 5%;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    height: 40px;
    border: 1.5px solid #2c3b4b;
    background-color: #fffef7; 
    padding-left: 1em;
    font-size: 15px;
}

#myInput::placeholder{
    color: rgba(0,0,0,.6)
    /* width: 70% */
}

#resultsDiv {
    display: grid;
    grid-template-columns: 1fr, 1fr ;
    grid-gap: 5px;
    grid-auto-columns: 50%;
}

.result {
    display: grid;
    grid-column: 1/2;
    width: 92%;
    margin: 1em;
    border: 2px solid #2c3b4b;
    background-color:#fffef7;
    color: rgba(0,0,0,.6)
}

.result:nth-child(even) {
    grid-column:2/2
}

.card {
    padding:.5em 1em
}

#myBtn {
    display: block;
    margin-top: 2.5em;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2.5em;
    background-color:#fffef7;
    height: 40px;
    width: 20%;
    font-size: 1.4em;
    color: #2c3b4b;
    font-family: 'Teko', sans-serif;
    padding-top: .21em;
}

#myBtn:focus {
    outline: none !important
}

.intro {
    font-family: 'EB Garamond 600', serif;
}

.ingredients {
    font-family: 'EB Garamond', serif;
}


.dropdown {
        display: none
    }


@media only screen and (max-width: 550px) {
    
    #resultsDiv {
        display: block;
    }
    
    .result {
        display: block;
        
    } 
    
    .result:nth-child(even) {
        display: block
    }


    
    
    
        
